#page {

  .educationContainer {
    .imgEducation {
      display: block;
    }

    .CoursesItem {
      background-repeat: no-repeat;
      h4 {
        font-size: 20px;
        font-family: $fontMedium;
        margin: 101px 0 47px 0;
        letter-spacing: 1.7px;
      }
      .CoursesItem_clock {
        font-size: 20px;
        font-family: $fontMedium;
        color: #cecece;
        letter-spacing: 1.7px;
        margin-bottom: 19px;
      }
      .CoursesItem_about {
        font-size: 15px;
        line-height: 24px;
        letter-spacing: 0px;
        text-align: justify;
        width: 370px;
        margin-bottom: 22px;
      }
      a {
        font-size: 15px;
        line-height: 24px;
        letter-spacing: 0.8px;
        color: #b69e62;
        img {
          margin-left: 17px;
        }
      }
      &:nth-child(1) {
        background-image: url('../img/W2.png');
        background-position: -3px 0;
      }
      &:nth-child(2) {
        background-image: url('../img/K2.png');
        background-position: 9px 0;
        padding-left: 9px;
        h4 {
          margin-bottom: 24px;
        }
      }
      &:nth-child(3) {
        background-image: url('../img/П2.png');
        background-position: 22px 0;
        padding-left: 22px;
        h4 {
          margin-bottom: 24px;
        }
      }
    }
    .formQuery {
      margin-top: 211px;
    }
  }
}
