#page {
  header {
    height: 233px;
  }

  .webdesignContainer, .poligrafContainer, .educationContainer, .employmentContainer {
    z-index: 8999;
    position: relative;
  }

  .sectionBlock {
      min-width: 1170px !important;
  }

  .grid {
    margin-top: -56px;
  }

  .grid-item {
    float: left;
    width: 370px;
    height: 260px;
    margin-bottom: 40px;
    &:hover {
      .selectItem {
      opacity: 1;
      transition: opacity, 1s;
      }
    }
  }

  .grid-item--width2 { width: 770px;}
  .grid-item--height2 { height: 560px; }

  .selectItem {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #B69E62;
    width: 100%;
    height: 100%;
    display: table;
    opacity: 0;
    transition: opacity, 1s;
    p {
      color: #fff;
      font-family: $font;
      font-size: 18px;
      display: table-cell;
      vertical-align: middle;
      text-align: left;
      width: 230px;
      padding: 0 70px;
    }
    a {
      color: #fff;
      border: 2px solid #fff;
      font-family: $font;
      font-size: 14px !important;
      display: block;
      width: 230px;
      padding: 10px 15px;
      text-align: center;
      margin: 15px auto 0 auto;
    }
  }

  .selectorPage {
    text-align: center;
    margin-top: 49px;
    margin-bottom: 104px;
    button {
      display: inline-block;
      width: 30px;
      height: 11px;
      margin: 0 17px 0 17px;
      background-color: #fff;
      border: 1px solid #bbbbbb;
    }
    #activePage {
      background-color: #bbbbbb;
    }
  }

  .UslugiPriceItem {
    background-repeat: no-repeat;
    h4 {
      color: #000; /* text color */
      font-family: $font;
      font-size: 20px;
      font-weight: bold;
      margin-top: 61px;
      letter-spacing: 1.9px;
      margin-bottom: 30px;
    }
    .UslugiPriceItem_about {
      font-size: 16px;
      font-family: $font;
      letter-spacing: 0.4px;
      margin-bottom: 35px;
    }
    .UslugiPriceItem_price {
      color: #b09251; /* text color */
      letter-spacing: 1.5px;
      font-family: $font;
      font-size: 14px;
    }
  }

  .webdesignContainer .UslugiPriceItem {
    &:nth-child(1) {
      background-image: url('../img/B.png');
      background-position: 15px 0;
    }
    &:nth-child(2) {
      background-image: url('../img/L.png');
      background-position: 16px 0;
    }
    &:nth-child(3) {
      background-image: url('../img/K.png');
      background-position: 16px 0;
    }
    &:nth-child(4) {
      background-image: url('../img/M.png');
      background-position: 15px 0;
    }
  }

  .poligrafContainer .UslugiPriceItem {
    &:nth-child(1) {
      background-image: url('../img/C.png');
      background-position: 15px 0;
    }
    &:nth-child(2) {
      background-image: url('../img/P.png');
      background-position: 16px 0;
    }
    &:nth-child(3) {
      background-image: url('../img/M.png');
      background-position: 16px 0;
    }
    &:nth-child(4) {
      background-image: url('../img/B.png');
      background-position: 15px 0;
    }
  }

  .formQuery {
    border: 1px solid #000;
    width: 570px;
    margin: 0 auto 170px auto;
    padding: 57px 70px 69px 69px;
    box-shadow: 30px 30px 0px 0px #f0f0f0;
    input[type="text"], input[type="email"], select {
      margin-bottom: 44px;
      width: 430px;
      border: none;
      border-bottom: 1px solid #000;
      font-size: 14px;
      font-family: $fontLight;
      padding: 7px 0;
      letter-spacing: 1.3px;
      outline: none;
      &:active, &:focus {
        border-bottom: 1px solid #b69e62;
      }
    }
    input[type="submit"] {
      margin-top: 10px;
      border: none;
      background-color: #b69e62;
      color: #fff;
      font-size: 14px;
      text-transform: uppercase;
      padding: 14px 134px;
      letter-spacing: 1.2px;
    }
  }

  .contactContainer #separator {
    margin-top: 92px;
    margin-bottom: 106px;
  }
}
