#page {

  .employmentContainer {
    .separator {
      margin-bottom: 89px;
    }
    #imgEmployment {
      display: block;
      margin: 0 -6px 0 0;
      float: right;
    }

    .aboutEmployment {
      position: absolute;
      top: 72px;
      left: -6px;
      width: 670px;
      background-color: #fafafa;
      padding: 61px 95px 41px 67px;
      p {
        font-family: $font;
        letter-spacing: .7px;
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 25px;
        text-align: justify;
      }
      .aboutEmployment__initial {
        font-size: 24px;
        color: #a78f54;
        font-family: $font;
      }
    }
    .partnerEmployment {
      font-family: GothamPro-Medium;
      text-align: center;
      font-size: 25px;
      text-transform: uppercase;
      margin-top: 156px;
      margin-left: -12px;
      letter-spacing: 3.8px;
    }
    .partnerEmployment__items {
      margin-top: 31px;
      .partnerEmployment__item {
        display: inline-block;
        margin: 0 55px 0 40px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
    .selectorPage {
      margin-bottom: 141px;
    }
  }
}
